
.contact {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white ;
    margin-bottom: 50px;
  }

  .contactGreeting{
    font-size: 30px;
    background-color: white;
    
  }
  .contactGreeting svg{
    font-size: 40px;
    transform: translateY(10px);
    color:#633509
  }
  .contactGreeting .hot{
    font-size: 25px;
    transform: translate(-33px,-25px);
  }
  input[type=text], .contactMessage{
    width: 80%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 10px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 16px;
    resize: none;
  }
 
  .contactSendButton{
    background-color: #04AA6D;
    color: white;
    padding: 10px 50px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 20px;

  }
  
  .contactSendButton:hover {
    background-color: #45a049;
  }
  
